<template>
  <div class="venue-selector-container">
    <van-popup
      v-model="show"
      position="bottom"
      round
      :close-on-click-overlay="false"
    >
      <van-picker
        class="picker-style"
        title="选择地点"
        show-toolbar
        :columns="columns"
        value-key="name"
        :default-index="provinceDefault"
        @confirm="onConfirm"
        @cancel="onCancel"
      />
    </van-popup>
  </div>
</template>

<script>
import pcasCode from "@/static/pcas-code";

export default {
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    // defaultActive: {
    //   type: String,
    //   default: "",
    // },
  },
  data() {
    return {
      columns: pcasCode,
      provinceDefault: 0,
      cityDefault: 0,
      areaDefault: 0,
      streetDefault: 0,
      // 地点默认选择配置项
      defaultActive: "",
    };
  },
  created() {
    console.log("pcasCode :>> ", pcasCode);
    this.defaultActive = this.$store.getters.associationInfo.default_address;
    this.handleStreetData(this.columns);
    this.handleDefaultActive(this.defaultActive);
  },
  methods: {
    handleDefaultActive(str) {
      const strArr = str.split(",");
      console.log("strArr :>> ", strArr);
      this.provinceDefault = parseInt(strArr[0]);
      this.cityDefault = parseInt(strArr[1]);
      this.areaDefault = parseInt(strArr[2]);
      this.streetDefault = parseInt(strArr[3]);
      this.handleStreetData(this.columns);
    },

    /**
     * 添加区域选择空选项
     */
    handleStreetData(list) {
      let newList = [];
      newList = Object.assign(list);
      const hashIf = (newList) => {
        for (const item of newList) {
          return item.code === "";
        }
      };
      for (const provinceItem of newList) {
        for (const cityItem of provinceItem.children) {
          const hasIf = hashIf(provinceItem.children);
          console.log("hasIf :>> ", hasIf);
          for (const areaItem of cityItem.children) {
            // 设置默认街道选项
            areaItem.defaultIndex = this.streetDefault;
            areaItem.children.push({
              code: "",
              name: "线上",
            });
          }
          // 设置默认区域选项
          cityItem.defaultIndex = this.areaDefault;
          cityItem.children.push({
            code: "",
            name: "线上",
            children: [
              {
                code: "",
                name: "线上",
              },
            ],
          });
        }
        // 设置默认城市选项
        provinceItem.defaultIndex = this.cityDefault;
        provinceItem.children.push({
          code: "",
          name: "线上",
          children: [
            {
              code: "",
              name: "线上",
              children: [
                {
                  code: "",
                  name: "线上",
                },
              ],
            },
          ],
        });
      }
    },

    /**
     * 确认
     */
    onConfirm(value, index) {
      const province = this.columns[index[0]].name;
      const city = this.columns[index[0]].children[index[1]].name;
      const district =
        this.columns[index[0]].children[index[1]].children[index[2]].name;
      const street =
        this.columns[index[0]].children[index[1]].children[index[2]].children[
          index[3]
        ].name;
      // console.log("省 :>> ", province);
      // console.log("市 :>> ", city);
      // console.log("区 :>> ",district);
      // console.log("街道 :>> ", street);
      const val = {
        province,
        city,
        district,
        street,
      };
      this.$emit("confirm", val);
      this.onCancel();
    },
    /**
     * 取消
     */
    onCancel() {
      this.$emit("update:show", false);
    },
  },
};
</script>

<style lang="scss" scoped>
.venue-selector-container {
  .picker-style {
    ::v-deep .van-picker__toolbar {
      margin: px(36) 0 0 0;
      .van-picker__cancel {
        font-size: px(32);
        color: #999999;
      }
      .van-picker__title {
        font-size: px(32);
        font-weight: bold;
      }
      .van-picker__confirm {
        font-size: px(32);
        font-weight: bold;
        color: #0077ff;
      }
    }
  }
}
</style>
